@import url(https://fonts.googleapis.com/css?family=Montserrat:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Cabin&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  width: 100%;
  height: 100vh;
}

#root {
  width: 100%;
  height: 100%;
}
.app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #1db954;
}
.input {
  display: flex;
  flex-direction: column;
  margin: 32px;
  background-color: #ffffff;
  border-radius: 20px;
  padding: 16px;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.32);
}

.searchBar {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.input input {
  font-family: 'Cabin', sans-serif;
  color: #8d8d8d;
  height: 56px;
  width: 392px;
  border: none;
  background-color: #0000001a;
  border-radius: 16px;
  padding: 16px;
  font-size: 20px;
  outline-width: 0;
  box-sizing: border-box;
}

.input button {
  height: 24px;
  width: 24px;
  margin-left: 16px;
  border: none;
  background-color: transparent;
  outline-width: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  fill: #8d8d8d;
}

.input button:hover {
  cursor: pointer;
}

.input button:active {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.32);
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.input button svg {
  padding-left: 4px;
  fill: #1db954;
}

.input .tooltip {
  font-family: 'Cabin', sans-serif;
  color: #8d8d8d;
  width: 100%;
  border-radius: 16px;
  padding: 64px 16px;
  font-size: 16px;
  text-align: center;
  box-sizing: border-box;
}
.input .result {
  display: flex;
  height: 56px;
  flex-direction: row;
  align-items: center;
  border-radius: 8px;
  box-sizing: border-box;
  padding: 8px 16px;
  margin: 8px 0;
}

.input .result:hover {
  background-color: #0000001a;
  cursor: pointer;
}

.input .result .cover {
  height: 40px;
  width: 40px;
  border-radius: 4px;
}

.input .result .info {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-start;
  font-family: 'Cabin', sans-serif;
  color: #8d8d8d;
  border-radius: 16px;
  margin-left: 16px;
  font-size: 16px;
  box-sizing: border-box;
}

.input .result .info .title {
  font-weight: bold;
  color: #000000;
  margin-bottom: 4px;
}
